.Home {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: aliceblue;
}

.home {
  height: 80%;
  width: 100%;
  display: flex;
  flex-flow: wrap;
}

.home h1 {
  font-size: 2.5rem;
  margin: 0;
}

.home h2 {
  margin-top: 0;
  margin-bottom: 0;
}

.home-picture {
  height: 100%;
  width: 50%;
}

.home-picture-img {
  height: 95%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.home-picture-img img {
  flex-shrink: 0;
  height: 100%;
  min-width: 100%;
}

.home-picture-desc {
  text-align: end;
}

.home-text {
  height: 90%;
  width: 40%;
  display: flex;
  flex-flow: wrap;
  align-content: space-between;
  padding: 2rem;
  padding-top: 0;
}

.home-text div {
  width: 100%;
}

.github {
  height: 4rem;
  display: inline-flex;
  align-items: center;
  text-align: center;
}

.github b {
  line-height: 1rem;
  font-size: 1.2rem;
  margin-left: 0.4rem;
}

@media screen and (max-width: 1325px) {
  .Home {
    height: fit-content;
  }

  .home {
    height: 100%;
  }

  .home h1 {
    font-size: 2rem;
  }

  .home-picture {
    width: 100%;
    padding-bottom: 2rem;
  }

  .home-picture-img {
    max-height: 60vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }

  .home-picture-img img {
    flex-shrink: 0;
    height: auto;
    max-width: 100vw;
  }

  .home-picture-desc {
    height: 25%;
    text-align: center;
  }

  .home-text {
    height: fit-content;
    width: 100%;
    padding: 2rem;
    padding-right: 5rem;
    padding-left: 5rem;
  }

  .home-text div {
    padding-bottom: 4rem;
  }
}

@media screen and (max-width: 1000px) {
  .home-text {
    padding: 1.5rem;
  }
}
